.container {
  height: 100%;
  width: 100vw;
  display: flex;
  justify-content: center;
}
.container .navbar {
  display: flex;
  width: 100%;
  height: 100px;
  position: absolute;
  align-items: center;
}
.container .navbar .title {
  padding-left: 90px;
  width: 20%;
}
.container .navbar .search {
  display: flex;
  justify-content: center;
  width: 60%;
}
.container .navbar .search .form {
  width: 100%;
}
.container .navbar .search .form .text {
  font-size: 1rem;
  padding: 0.7rem 0;
  border: none;
  border-bottom: 1px solid #ddd;
  border-radius: 15px;
  width: 60%;
}
.container .navbar .search .form .date {
  font-size: 1rem;
  padding: 0.7rem 0;
  border: none;
  width: 20%;
  border-bottom: 1px solid #ddd;
  border-radius: 15px;
  padding-left: 15px;
  margin-left: 10px;
}
.container .navbar .search .form .btn {
  transition: all 0.3s;
  color: #fff;
  font-size: 1rem;
  padding: 0.7rem 0;
  border: none;
  border-radius: 15px;
  width: 15%;
  background-color: rgb(80, 156, 214);
  cursor: grab;
  margin-left: 10px;
}
.container .navbar .search .form .btn:hover {
  background-color: rgb(0, 84, 149);
}

.loader {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 300;
}

.movies {
  display: grid;
  grid-template-columns: repeat(2, minmax(500px, 1fr));
  grid-gap: 80px;
  width: 90%;
  padding: 150px 0 80px 0;
}

@media screen and (max-width: 1090px) {
  .movies {
    grid-template-columns: 1fr;
    width: 100%;
  }
}/*# sourceMappingURL=Home.module.css.map */