.movieCard {
  display: grid;
  grid-template: 7fr 1.6fr / 1fr 3fr;
  grid-template-areas:
    "left right"
    "footer footer";
  background-color: white;
  height: 80%;
  width: 100%;
  position: relative;
  border-radius: 15px;
  box-shadow: 0 5px 20px 5px rgba(0, 0, 0, 0.5);
  .left {
    grid-area: left;
    width: 100%;

    .poster {
      position: absolute;
      height: 300px;
      top: -5%;
      left: -5%;
      width: auto;
      z-index: 1; /* img 요소를 최상위로 배치합니다. */
      box-shadow: 0 5px 20px 5px rgba(0, 0, 0, 0.5); /* 그림자 추가 */
    }
    @media screen and (max-width: 1090px) {
      .poster {
        height: 400px;
        width: auto;
        padding-left: 50px;
      }
    }
  }
  .right {
    grid-area: right;
    width: 100%;
    .title {
      font-size: 30px;
      font-weight: 600;
      margin-bottom: 5px;
      padding-left: 90px;
    }
    .plot {
      //   position: relative;
      z-index: 1; /* plot 요소를 img 요소 위에 배치합니다. */
      padding: 20px 20px 10px 90px; /* plot 요소의 패딩을 조정하여 img 요소와 겹치게 만듭니다. */
    }
  }
  .footer {
    grid-area: footer;
    ul {
      display: flex;
      justify-content: space-around;
      padding: 0;
      li {
        list-style: none;
        font-size: 25px;
        font-weight: 600;
        color: #333;
      }
    }
  }
}
